import React from "react";
// @material-ui/core components
import withStyles from "@material-ui/core/styles/withStyles";
// @material-ui/icons

// core components
import GridContainer from "components/Grid/GridContainer.jsx";
import GridItem from "components/Grid/GridItem.jsx";

//import completedStyle from "assets/jss/material-kit-react/views/componentsSections/completedStyle.jsx";
import productStyle from "assets/jss/material-kit-react/views/landingPageSections/productStyle.jsx";

class SectionTerritorio extends React.Component {
  render() {
    const { classes } = this.props;
    return (
      <div className={classes.section}>
        <div className={classes.container}>
          <GridContainer justify="center">
            <GridItem xs={12} sm={12} md={8}>
              <h2 className={classes.title}>Territorio</h2>
              <h4 className={classes.description}>
              L'Irpinia si estende sulla parte centro-orientale della Campania, non ha uno sbocco al mare e presenta un territorio prevalentemente montuoso.
              <br />
              Il territorio si presenta come un altipiano inciso da valli e punteggiato da rilievi, tra i quali serpeggiano numerosi fiumi e torrenti. Tra questi si possono annoverare il Calore Irpino, l'Ofanto e il Sele.
              <br />
              
              Per quanto riguarda l'orografia del territorio, le cime più imponenti si ergono nella zona sud-occidentale e sarebbero
              i monti Cervialto, la cui cima si trova nel Comune di Calabritto, ed il Terminio, in quello di Montella, 
              massicci di origine carsica rispettivamente di 1809 e 1806 m s.l.m.
              <br />
              La parte sud-orientale della dorsale prende il nome di “Altopiano del Formicoso” e ha un'altitudine media 
              di circa 800 m s.l.m.
              <br />
              Principali attrattive turistiche sono il comprensorio sciistico del Laceno, con il lago omonimo,
               il monte Terminio oltre ad alcuni borghi che fanno parte dell'associazione dei borghi più belli d'Italia
                (quali Monteverde, Nusco, Savignano Irpino e Zungoli).
                <br />
              Fra le strutture architettoniche spicca il castello di Ariano Irpino, città sede della prima contea 
              normanna dell'Italia meridionale ove venne emanato il corpus giuridico delle Assise di Ariano. 
              Notevole anche il castello aragonese di Monteverde, sede dal 1532 al 1641 della signoria dei Grimaldi 
              principi di Monaco. Di rilievo, inoltre, è il centro storico di Gesualdo con il castello che fu dimora 
              del Principe dei Musici Carlo Gesualdo, Rocca San Felice, Calitri (particolarmente Borgo Castello) 
              e il borgo medievale di Castelvetere. Importante anche il castello di San Barbato, forte costruito 
              in epoca longobarda nell'849. Le prime notizie sull'esistenza del castello risalgono a un documento 
              del 1146 in cui compare il nome di Malfrido de Sancto Barbato.
              <br />
              Fra le mete religiose si citano il santuario di Montevergine in territorio di Mercogliano, 
              il santuario di San Gerardo Maiella a Caposele, i due santuari medievali di San Liberatore 
              e della Madonna di Valleluogo presso Ariano Irpino, il santuario di Carpignano di Grottaminarda
               e il santuario della Madonna del Buon Consiglio a Frigento. 
              </h4>
            </GridItem>
          </GridContainer>
        </div>
      </div>
    );
  }
}

export default withStyles(productStyle)(SectionTerritorio);
